import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';

const wtMountDiv = "wt-auto-complete-v1";
const wtMounter = document.getElementById(wtMountDiv);
const agentCode = wtMounter.dataset.agentcode;
const mode = wtMounter.dataset.mode;
const showLogo = wtMounter.dataset.showlogo === "true";
const highlightColor = wtMounter.dataset.highlightcolor;
const font = wtMounter.dataset.font;
ReactDOM.render(<App 
                    agentCode={agentCode}
                    mode={mode}
                    showLogo={showLogo}
                    highlightColor={highlightColor}
                    font={font} 
                />, wtMounter);




