import Autosuggest from 'react-autosuggest';
import React, { Component } from 'react';
import AutosuggestHighlightParse from './Parse';
import AutosuggestHighlightMatch from './Match';

class AutoSuggestWrapper extends Component {

    constructor(props) {
        super(props);
        this.state = {
            value: '',
            suggestions: [],
            iframeIsOpen: false,
            baseUrl: "https://my.wordtravels.com",
            guideUrl: undefined,
        };
    }

    getSuggestions = value => {
        const { data } = this.props;
        const inputValue = value.trim().toLowerCase();
        const inputLength = inputValue.length;

        return inputLength === 0 ? [] : data.filter(lang =>
            lang.name.toLowerCase().slice(0, inputLength) === inputValue
        );
    };

    getSuggestionValue = suggestion => {
        return `${suggestion.name}, ${suggestion.parent}`;
    }

    selected = (event, selected) => {
        this.setState({ guideUrl: selected.suggestion.url });
    }

    renderSuggestion = (suggestion, { query }) => {
        const matches = AutosuggestHighlightMatch(`${suggestion.name}, ${suggestion.parent}`, query);
        const parts = AutosuggestHighlightParse(`${suggestion.name}, ${suggestion.parent}`, matches);

        return (
            <span>
                {parts.map((part, index) => {
                    const className = part.highlight ? 'react-autosuggest__suggestion-match' : null;

                    return (
                        <span className={className} key={index}>
                            {part.text}
                        </span>
                    );
                })}
            </span>
        );
    }

    onChange = (event, { newValue }) => {
        this.setState({
            value: newValue
        });
    };

    onSuggestionsFetchRequested = ({ value }) => {
        this.setState({
            suggestions: this.getSuggestions(value)
        });
    };

    onSuggestionsClearRequested = () => {
        this.setState({
            suggestions: []
        });
    };

    render() {
        const { value, suggestions, iframeIsOpen, guideUrl, baseUrl } = this.state;
        const { agentCode, mode, showLogo, highlightColor, font } = this.props;

        const inputProps = {
            placeholder: 'Search destination',
            value,
            onChange: this.onChange
        };

        return (
            <div id="wt-wrapper">
                <Autosuggest
                    suggestions={suggestions}
                    onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                    onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                    getSuggestionValue={this.getSuggestionValue}
                    renderSuggestion={this.renderSuggestion}
                    onSuggestionSelected={this.selected}
                    inputProps={inputProps}
                />
                {guideUrl &&
                    <iframe
                        src={`${baseUrl}${guideUrl}?agent=${agentCode}&showlogo=${showLogo}&highlightcolor=${highlightColor}&font=${font}`}>
                    </iframe>}
            </div>
        );
    }
}

export default AutoSuggestWrapper;




